.container {
  padding-left: 10px;
  padding-right: 10px;
}

.container .row {
  margin-left: -10px;
  margin-right: -10px;
}

.container .row [class*="col-"] {
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
  margin-bottom: 20px;
}

@media (min-width: 980px) {
  .container .row {
    display: flex;
  }
  .container .row .col-1-12 {
    width: 8.33333%;
  }
  .container .row .col-2-12 {
    width: 16.66667%;
  }
  .container .row .col-3-12 {
    width: 25%;
  }
  .container .row .col-4-12 {
    width: 33.33333%;
  }
  .container .row .col-5-12 {
    width: 41.66667%;
  }
  .container .row .col-6-12 {
    width: 50%;
  }
  .container .row .col-7-12 {
    width: 58.33333%;
  }
  .container .row .col-8-12 {
    width: 66.66667%;
  }
  .container .row .col-9-12 {
    width: 75%;
  }
  .container .row .col-10-12 {
    width: 83.33333%;
  }
  .container .row .col-11-12 {
    width: 91.66667%;
  }
  .container .row .col-12-12 {
    width: 100%;
  }
}

.page-header .container {
  max-width: 1340px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 80px;
}

.page-logo {
  width: 145px;
  height: 70px;
  background: #FF4486;
  border-radius: 6px;
  display: block;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Oleo Script", sans-serif;
  color: white;
  font-size: 50px;
  text-decoration: none;
}

.page-nav-list {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.page-nav-list li {
  margin-left: 30px;
}

.page-nav-list a {
  font-weight: bold;
  text-decoration: none;
  color: #444;
  display: block;
  padding: 15px;
  border-radius: 6px;
  transition: 0.6s;
}

.page-nav-list li.active a {
  background: #444;
  color: #fff;
}

.page-nav-list a:hover {
  background: #fff;
}

.page-nav-toggle {
  width: 60px;
  height: 40px;
  border: 0;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  position: fixed;
  top: 15px;
  left: 15px;
  cursor: pointer;
  display: none;
  z-index: 100;
}

.page-nav-toggle strong {
  position: absolute;
  left: -999px;
}

.page-nav-toggle span {
  width: 15px;
  height: 4px;
  background: #fff;
  display: block;
  margin: 1px auto;
}

@media (max-width: 980px) {
  .page-nav-list {
    display: none;
  }
  .page-header .container {
    justify-content: center;
    margin-top: 50px;
  }
  .page-nav-toggle {
    display: block;
  }
}

.welcome {
  padding: 95px 85px 40px;
  margin-top: 50px;
}

.welcome-right {
  text-align: center;
}

.welcome-right .btn {
  max-width: 465px;
  width: 100%;
  height: 100px;
  font-size: 40px;
  font-weight: 800;
  display: inline-flex;
  justify-content: space-between;
  flex-direction: row-reverse;
  align-items: center;
  margin-bottom: 20px;
}

.welcome-title {
  font-size: 60px;
  font-weight: 800;
  margin-top: 0;
}

.welcome-text {
  font-size: 45px;
  font-weight: 300;
  color: #666;
}

@media (max-width: 1100px) {
  .welcome-title {
    font-size: 55px;
  }
  .welcome-text {
    font-size: 40px;
  }
  .welcome-right .btn {
    max-width: 100%;
    display: flex;
    font-size: 36px;
  }
}

@media (max-width: 980px) {
  .welcome-left {
    text-align: center;
  }
  .welcome-right {
    margin-top: 50px;
  }
  .welcome-right .btn {
    justify-content: center;
    flex-direction: row;
  }
  .welcome-right .btn .fas {
    margin-right: 30px;
  }
}

@media (max-width: 500px) {
  .welcome {
    padding: 50px 30px 30px 30px;
  }
  .welcome-title {
    font-size: 33px;
    font-weight: 800;
    margin-top: 0;
  }
  .welcome-text {
    font-size: 22px;
    font-weight: 300;
    color: #666;
  }
  .welcome-right {
    margin-top: 50px;
  }
  .welcome-right .btn {
    justify-content: center;
    flex-direction: row;
    width: 100%;
    height: 70px;
    max-width: 465px;
    font-size: 25px;
  }
  .welcome-right .btn .fas {
    margin-right: 20px;
  }
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", sans-serif;
  background: #eee;
  color: #444;
  overflow-x: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1340px;
}

.card {
  background: #FFF;
  border-radius: 6px;
  box-shadow: 0 3px 0 0 rgba(0, 0, 0, 0.1);
}

.btn {
  font-weight: bold;
  border-radius: 6px;
  display: inline-block;
  text-decoration: none;
  font-size: 20px;
  color: #fff;
  padding: 20px 40px;
}

.btn-primary {
  background: #0090C5;
  color: #fff;
}

.btn-secondary {
  background: #eee;
  color: #444;
}

.btn-more {
  background-color: #0090C5;
  color: #fff;
}

.mobile-page-nav-list {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 300px;
  margin: 0;
  padding: 0;
  background: #fff;
  list-style: none;
  text-decoration: none;
  box-shadow: 1px 0 3px rgba(0, 0, 0, 0.3), 5px 0 3px rgba(0, 0, 0, 0.1);
  display: none;
}

.mobile-page-nav-list li {
  border-bottom: 1px solid #eee;
}

.mobile-page-nav-list a {
  display: block;
  text-decoration: none;
  padding: 20px;
  text-align: center;
  font-weight: bold;
  color: #444;
  transition: 0.2s;
}

.mobile-page-nav-list a:hover {
  background: #444;
  color: #fff;
}

@media (max-width: 980px) {
  .mobile-page-nav-list {
    display: block;
    left: -300px;
    transition: 0.5s;
  }
  .all {
    transition: 0.5s;
    overflow: hidden;
  }
  .nav-show .mobile-page-nav-list {
    transform: translateX(100%);
  }
  .nav-show .all {
    transform: translateX(300px);
  }
}

section.article-list {
  padding-top: 60px;
}

.article {
  font-size: 20px;
}

.article-photo img {
  width: 100%;
  height: auto;
  border-radius: 6px 6px 0 0;
}

.article-title {
  font-size: 30px;
  font-weight: 800;
  margin: 65px 40px 10px;
}

.article-subtitle {
  margin: 0 40px;
  font-weight: 400;
  margin-bottom: 40px;
}

.article-text {
  margin: 0 40px;
  line-height: 2em;
  margin-bottom: 40px;
  color: #888;
  padding-bottom: 40px;
}

@media (max-width: 500px) {
  .article-title {
    font-size: 24px;
    font-weight: 800;
    margin: 35px 30px 10px;
  }
  .article-subtitle {
    font-size: 14px;
    margin: 0 30px;
    font-weight: 300;
    margin-bottom: 30px;
  }
  .article-text {
    font-size: 14px;
    margin: 10px 30px;
    line-height: 2em;
    color: #888;
  }
}

section.article-stuff {
  max-width: 100%;
  background-color: #fff;
  padding-top: 70px;
  padding-bottom: 60px;
  display: flex;
}

.stuff-left .left-title-stuff {
  font-size: 24px;
  font-weight: 900;
  margin-bottom: 30px;
}

.stuff-left .first-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 7px;
}

.stuff-left .second-row {
  display: flex;
  justify-content: space-between;
}

.stuff-left .stuff-photo {
  width: 195px;
  height: 120px;
}

.stuff-left .stuff-photo img {
  width: 100%;
  height: auto;
  border-radius: 7px;
}

.stuff-left .btn-more {
  margin-top: 60px;
  display: inline-block;
  background: #0090C5;
  color: #fff;
  text-decoration: none;
  border-radius: 6px;
  font-weight: 800;
  cursor: pointer;
  font-size: 1.35em;
  padding: 0.9em 1.5em;
  transition: 0.3s;
}

.stuff-left .btn-more:hover {
  background-color: #009fd9;
}

.stuff-left .btn-more .far {
  margin-right: 8px;
}

.stuff-right .right-title-stuff {
  font-weight: 900;
  font-size: 38px;
  margin-bottom: 38px;
}

.stuff-right .text-first {
  font-size: 18px;
  line-height: 2.4rem;
  color: #888;
  font-weight: 200;
  margin-bottom: 30px;
}

.stuff-right .text-second {
  font-size: 18px;
  line-height: 2.4rem;
  color: #888;
  font-weight: 200;
}

.stuff-right .btn-continue {
  margin-top: 40px;
  display: inline-block;
  background: #0090C5;
  color: #fff;
  text-decoration: none;
  border-radius: 6px;
  font-weight: 800;
  cursor: pointer;
  font-size: 1.35em;
  padding: 0.9em 1.5em;
  transition: 0.3s;
}

.stuff-right .btn-continue:hover {
  background-color: #009fd9;
}

.stuff-right .btn-continue .fas {
  padding-right: 8px;
}

@media (max-width: 980px) {
  section.article-stuff {
    padding-top: 50px;
  }
  .row-children {
    display: flex;
    flex-direction: column-reverse;
  }
  .stuff-right {
    margin: 0 30px 0 30px;
    border-bottom: 1px solid #eee;
    padding-bottom: 50px;
  }
  .stuff-right .right-title-stuff {
    font-weight: 900;
    font-size: 38px;
    margin-bottom: 28px;
  }
  .stuff-right .text-first {
    font-size: 17px;
  }
  .stuff-right .text-second {
    font-size: 17px;
  }
  .stuff-right .btn-continue {
    width: 100%;
    text-align: center;
  }
  .stuff-left {
    margin: 0 30px 0 30px;
  }
  .stuff-left .left-title-stuff {
    font-size: 21px;
  }
  .stuff-left .first-row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
  }
  .stuff-left .second-row {
    display: flex;
    justify-content: space-between;
  }
  .stuff-left .stuff-photo {
    width: 320px;
    height: 195px;
  }
  .stuff-left .stuff-photo img {
    width: 100%;
    border-radius: 7px;
  }
  .stuff-left .btn-more {
    width: 100%;
    text-align: center;
  }
  .stuff-left .btn-more .far {
    margin-right: 8px;
  }
}

@media (max-width: 500px) {
  section.article-stuff {
    padding-top: 20px;
  }
  .row-children {
    display: flex;
    flex-direction: column-reverse;
  }
  .stuff-right {
    border-bottom: 1px solid #eee;
    padding-bottom: 50px;
  }
  .stuff-right .right-title-stuff {
    font-weight: 900;
    font-size: 20px;
    margin-bottom: 16px;
  }
  .stuff-right .text-first {
    font-size: 15px;
    line-height: 1.8rem;
    width: 300px;
  }
  .stuff-right .text-second {
    font-size: 15px;
    line-height: 1.8rem;
    width: 300px;
  }
  .stuff-right .btn-continue {
    width: 100%;
    text-align: center;
    margin-top: 20px;
    width: 300px;
    font-size: 18px;
    padding: 0.8em 1.4em;
  }
  .stuff-left .left-title-stuff {
    font-size: 18px;
    margin-bottom: 20px;
  }
  .stuff-left .first-row {
    display: flex;
    justify-content: space-around;
  }
  .stuff-left .second-row {
    display: flex;
    justify-content: space-around;
  }
  .stuff-left .stuff-photo {
    width: 135px;
