  }
  .stuff-left .stuff-photo img {
    width: 100%;
    height: auto;
    border-radius: 7px;
  }
  .stuff-left .btn-more {
    width: 100%;
    font-size: 18px;
    margin-top: 40px;
    padding: 0.8em 1.4em;
  }
}

